import React from 'react'
import Link from 'gatsby-link'

import Layout from 'components/layout'

const IndexPage = () => (
  <Layout>
    <div>
      <h1>Welcome to my personal home page!</h1>
      <p>Look on some simple examples below:</p>
      <ul>
        <li>
          <Link to="/simple/">Simple example</Link>
        </li>
        <li>
          <Link to="/medium/">More complex example</Link>
        </li>
      </ul>
      <p>See also:</p>
      <ul>
        <li>
          <Link to="/board/">DnD Agile Board (angular, typescript)</Link>
        </li>
        <li>
          <Link to="/employees-list/">
            Build dynamic list entries (react, redux)
          </Link>
        </li>
        <li>
          <Link to="/jstest/">Simple JS test (vue)</Link>
        </li>
      </ul>
    </div>
  </Layout>
)

export default IndexPage
